import React from 'react';
import { RouteComponentProps } from 'react-router';
import { Tabs, Upload, message, Form, Input, Select, Button, List, Switch } from 'antd';
import {  } from '@ant-design/icons'
import './index.scss';

const { TabPane } = Tabs;
const {Option} = Select;
export default class UserSetting extends React.Component<RouteComponentProps> {
  
  state = {
    imgUrl: '',
    safeData: [
      {
        title: 'Ant Design Title 1',
      },
      {
        title: 'Ant Design Title 2',
      },
      {
        title: 'Ant Design Title 3',
      },
      {
        title: 'Ant Design Title 4',
      },
    ],
    notifyData: [
      {
        title: ''
      },
      {
        title: ''
      },
      {
        title: ''
      }
    ]
  }

  beforeUpload(file: File) {
    const isJpgOrPng = file.type === 'image/jpeg' || file.type === 'image/png';
    if (!isJpgOrPng) {
      message.error('You can only upload JPG/PNG file!');
    }
    const isLt2M = file.size / 1024 / 1024 < 2;
    if (!isLt2M) {
      message.error('Image must smaller than 2MB!');
    }
    return isJpgOrPng && isLt2M;
  }

  changeFile(info: any) {
    if (info.file.status === 'uploading') {
      this.setState({ loading: true });
      return;
    }
    if (info.file.status === 'done') {
      // Get this url from response in real world.
      this.getBase64(info.file.originFileObj, (imgUrl: string) =>
        this.setState({
          imgUrl,
          loading: false,
        }),
      );
    }
  }

  getBase64(img: File, callback: Function) {
    const reader = new FileReader();
    reader.addEventListener('load', () => callback(reader.result));
    reader.readAsDataURL(img);
  }

  updateData(values: any) {

  }
  
  render() {
    const {imgUrl, safeData, notifyData} = this.state
    return (
      <div className="user-setting">
        <Tabs 
          tabPosition='left'
          defaultActiveKey="1">
          <TabPane tab="" key="1">
            <div className="basic">
              <Upload
                name="avatar"
                listType="picture-card"
                className="avatar-uploader"
                showUploadList={false}
                action="https://www.mocky.io/v2/5cc8019d300000980a055e76"
                beforeUpload={(file) => this.beforeUpload(file)}
                onChange={(info) => this.changeFile(info)}
              >
                {imgUrl ? <img src={imgUrl} alt="avatar" style={{ width: '100%' }} /> : <div className="ant-upload-text"></div>}
              </Upload>
              <Form
                labelCol={{ span: 4 }}
                wrapperCol={{ span: 16 }}
                name="nest-messages"
                onFinish={(values) => this.updateData(values)}>
                <Form.Item name={['user', 'name']} label="" rules={[{ required: true }]}>
                  <Input />
                </Form.Item>
                <Form.Item name={['user', 'sex']} label="">
                  <Select defaultValue="1" style={{ width: 120 }}>
                    <Option value="1"></Option>
                    <Option value="2"></Option>
                  </Select>
                </Form.Item>
                <Form.Item name={['user', 'email']} label="" rules={[{ type: 'email' }]}>
                  <Input />
                </Form.Item>
                <Form.Item name={['user', 'website']} label="">
                  <Input />
                </Form.Item>
                <Form.Item name={['user', 'introduction']} label="">
                  <Input.TextArea />
                </Form.Item>
                <Form.Item wrapperCol={{offset: 4}}>
                  <Button type="primary" htmlType="submit">
                    
                  </Button>
                </Form.Item>
              </Form>
            </div>
          </TabPane>
          <TabPane tab="" key="2">
            <div className="safe">
              <List
                itemLayout="horizontal"
                dataSource={safeData}
                renderItem={(item: any) => (
                  <List.Item>
                    <List.Item.Meta
                      title={<a href="https://ant.design">{item.title}</a>}
                      description="Ant Design, a design language for background applications, is refined by Ant UED Team"
                    />
                  </List.Item>
                )}
              />
            </div>
          </TabPane>
          <TabPane tab="" key="3">
            <div className="notification">
            <List
                itemLayout="horizontal"
                dataSource={notifyData}
                renderItem={(item: any) => (
                  <List.Item>
                    <List.Item.Meta
                      title={<a href="https://ant.design">{item.title}</a>}
                      description=""
                    />
                    <div className="">
                      <Switch defaultChecked />
                    </div>
                  </List.Item>
                )}
              />
            </div>
          </TabPane>
        </Tabs>
      </div>
    )
  }
}